import { observer, inject } from 'mobx-react'
import React, { useState, useEffect } from 'react';
import { _formatDate, _coinAddress } from '@/assets/utils/comMethod'
import H5HECOmainPicture from './img/h5_heco_main_picture.png'
import H5BSCmainPicture from './img/h5_bsc_main_picture.png'
import HECOmainPicture from './img/heco_main_picture.png'
import BSCmainPicture from './img/bsc_main_picture.png'
import { chainList } from '@/assets/utils/comStatic'
import { contSet } from '@/assets/utils/comCss'
import {useHistory} from 'react-router-dom'
import styles from './Banner.module.scss'
import h5Curve from './img/h5_curve.png'
import curve from './img/curve.png'
import first from './img/first.png'


const Banner = ({ store })=> {

  let history = useHistory()

  // 語言、皮膚、設備
  const { lang, skin, config, imo } = store 

  // 保存列表數據
  const [imoProList, setImoProList] = useState([{
    name: 'Coming soon',
    time: 'Stay Tuned',
    pic: first
  },{
    name: 'Coming soon',
    time: 'Stay Tuned',
    pic: first
  },{
    name: 'Coming soon',
    time: 'Stay Tuned',
    pic: first
  },])

  // 鏈類型
  const sk = skin.skinSign

  // 國際化
  const bannerTextObj = lang[lang['language']]['Banner']

  useEffect(() => {
    // requestProList()
  },[]) //eslint-disable-line react-hooks/exhaustive-deps

  //获取推薦项目列表
  const requestProList = () => {
    imo.getImoProList({chain: chainList[sk], is_recommend: 1}).then((res) => {
      console.log('获取IMO推薦列表:', res)
      if (res.code === 0) { 
        const array = res.data.rows
        const item = array ? array : []
        setImoProList(item.slice(0, 3))
      }
    })
  }

  // 路由跳转
  const routerJumpHandle = (id) => {
    history.push({pathname: `/detail/${id}`}) 
  }

  // 廣告位渲染
  const cardRender = () => {
    let list = []
    imoProList.forEach((item, index) => {
      list.push(
        <li key={`cardRender${index}`} style={(config.device === 'pc' && imoProList.length < 3) ? contSet.mrDis(30) : {} }>
          <div className={styles.flex}>
            {/* <div className={styles.cardPic}><img src={_coinAddress(sk, item.getTokenAddr, item.type)} alt="" /></div> */}
            <div className={styles.cardPic}><img src={item.pic} alt="" /></div>
            <div>
              <p className={styles.cardTitle}>{item.name}</p>
              <div className={styles.cardTime}>
                {/* {_formatDate((item.startTime)*1000, 12)} SCT start */}
                {item.time}
                <span></span>
              </div>
            </div>
          </div>
        </li>
      )
    })
    return list
  }

  return (
    <div className={`${styles.banner} ${styles[skin.skinSign]}`}>
      <div className={styles.main}>
        <div className={styles.content}>
          <p className={styles.title}>{bannerTextObj['Title']}</p>
          <div className={styles.introduce}>
            <span></span>
            <p>{bannerTextObj['Introduce']}</p>
          </div>
          <div className={styles.curve}><img src={config.device === 'pc' ? curve : h5Curve} alt=''/></div>
          <p className={styles.protext}>{bannerTextObj['Rollout']}</p>
        </div>
        <div className={styles.picture}><img src={skin.skinSign === 'BSC' ? (config.device === 'pc' ? BSCmainPicture : H5BSCmainPicture) : (config.device === 'pc' ? HECOmainPicture : H5HECOmainPicture) } alt=''/></div>
      </div>
      <div className={styles.card}>
        <ul className={`${imoProList.length > 2 && styles.itemWrap }`}>{cardRender()}</ul>
        {/* <div className={styles.more}>{bannerTextObj['ShowProject']}</div> */}
      </div>
    </div>
  )
}

export default inject('store')(observer(Banner))
