// import { Spin } from 'antd'
import React, { useState, useEffect } from 'react';
import { observer, inject } from 'mobx-react'
import { chainList } from '@/assets/utils/comStatic'
import { contSet } from '@/assets/utils/comCss'
import styles from './Active.module.scss'
import Card from '@/layouts/Card/Card'

const Active = ({ store })=> {

  const { lang, imo, config, skin, contract } = store
  const [tabStatus, setTabStatus] = useState(1);
  const [imoProList, setImoProList] = useState([]);
  // const [loadStatus, setLoadStatus] = useState(true)
  const tabTextObj = lang[lang['language']]['Tab']
  const size = imoProList.length
  const statusList = {
    1: 2,
    2: 1
  }

  // 智能链
  const sk = skin.skinSign

  useEffect(() => {
    requestProList(2, 1)
  },[])// eslint-disable-line react-hooks/exhaustive-deps

  // 获取项目列表
  const requestProList = (status, index) => {
    imo.getImoProList({status, chain: chainList[sk]}).then((res) => {
      console.log('获取IMO列表:', res)
      if (res.code === 0) { 
        const array = res.data.rows
        const item = array ? array : []
        setImoProList(item.slice(0, 3))
        setTabStatus(index)
        // setLoadStatus(false)
        imo.saveImoProList(array)
      } else {
        // if(!loadStatus) setLoadStatus(true)
      }
    })
  }

  // 获取我参与的项目列表
  const requestProVote = (token) => {
    imo.getImoProVote({user_addr: token, chain:chainList[sk]}).then((res) => {
      console.log('获取我参与的IMO列表:', res)
      if (res.code === 0) { 
        const array = res.data.rows
        const item = array ? array : []
        setImoProList(item.slice(0, 3))
        localStorage.setItem('myInvestment',JSON.stringify(array))
        setTabStatus(3)
        // setLoadStatus(false)
      } else {
        // if(!loadStatus) setLoadStatus(true)
      }
    })
  }
  

  // 切换tab
  const switchTabHandle = (index) => {
    if(index === 3) {
      if(tabStatus !== 3 ) requestProVote(contract.accounts)
    } else {
      if(tabStatus !== index ) requestProList(statusList[index], index)
    }
  }
  

  // 卡片渲染
  const cardRender = () => {
    let list = []
    imoProList.forEach((item, index) => {
      list.push(
        <div style={size< 3 ? contSet.mrDis(30) : {}} key={`cardRender${Math.random()}`}><Card param={item} subscript={tabStatus} /></div>)
    })
    return list
  }

  // TAB渲染
  const tabRender = () => {
    let list = []
    const textArr = contract.accounts ? ['Processing', 'Start', 'Participated'] : ['Processing', 'Start']
    textArr.forEach((item, index) => {
      list.push(
        <span 
          key={`TabRender${item}`} 
          className={tabStatus === index+1 ? styles.select : ''  }
          onClick={() => switchTabHandle(index+1)}>{tabTextObj[item]}
        </span>
      )
    })
    return list
  }

  return (
    <div className={styles.main}>
      <div className={styles.tabBar}> 
        <div className={styles.tab}>{tabRender()}</div>
      </div>
      {/* <div className={`${loadStatus ? styles.cardBar : ''}`}> */}
      <div className={styles.cardBar}>
        {/* <Spin tip="Loading..." spinning={loadStatus}></Spin> */}
        <div className={`${config.device === 'pc' ? (size>2 ? styles.between : styles.df) : ''}`} key={tabStatus}>
          {size > 0 ? cardRender() : (<div className={styles.noData}>暂无数据</div>)}
        </div>
      </div>
    </div>
  )
}

export default inject('store')(observer(Active))
