import React from 'react';
import { Tooltip } from 'antd'
import { observer } from 'mobx-react'
import {useHistory} from 'react-router-dom'
import { _coinAddress, _sliceData } from '@/assets/utils/comMethod'
import CurrentCountDown  from '@/layouts/CountDown/CurrentCountDown'
import { _accDiv, _accMul } from '@/assets/utils/accuracy'
import { contSet } from '@/assets/utils/comCss'
import Strip from '@/layouts/Strip/Strip'
import styles from './Card.module.scss'
import audit from './img/audit.png'
import topic from './img/topic.png'
import store from '@/store/index'
import warn from './img/warn.png'


const Card = (props)=> {

  let history = useHistory()
  const { lang, skin, imo } = store
  const activeTextObj = lang[lang['language']]['Active']
  const investmentTextObj = lang[lang['language']]['Investment']
  const tabTextObj = lang[lang['language']]['Tab']
  const item = props.param

  // 智能链
  const sk = skin.skinSign

  // 状态样式区分
  const statusClassHandle = (val) => {
    return `${styles.status} ${styles[`status${val}`]}`
  }
  
  // 状态文字区分
  const statusTextHandle = (val) => {
    const textList = {
      1: activeTextObj['ComeS'],
      2: activeTextObj['End'],
      3: activeTextObj['Inli'],
      4: activeTextObj['Over']
    }
    return textList[val]
  }
  

  // 类型转译
  const transferTypeHandle = (val) => {
    const typeTextList = {
      1: activeTextObj['Overraise'],
      2: activeTextObj['Whitelist']
    } 
    return typeTextList[val]
  }

  // 转义TP
  const transerTp = item.toTokenSymbol.includes('LP') ? 'LP' : item.toTokenSymbol

  // 路由跳转
  const routerJumpHandle = () => {
    history.push({pathname: `/detail/${item.id}`}) 
  }

  const ratio = _sliceData(_accDiv(item.getTokenTotalAmount, item.toTokenTotalAmount), 8, true)

  const price = _accMul(ratio, item.toTokenPrice)

  const undone = item.status === 4 && (item.toTokenVotedAmount < item.toTokenTotalAmount)
  return (
    <div className={`${styles.card} ${styles[skin.skinSign]}`} onClick={() => routerJumpHandle()}>
      <div className={styles.cardTop}>
        <div className={styles.content}>
          <div className={styles.titleBar}>
            <div className={styles.proPic}>
              <img src={_coinAddress(sk, item.getTokenAddr, item.type)} alt=''/>
            </div>
            <div>
              <p className={styles.title}>{item.name.toUpperCase()}
              {item.approve ? <img src={audit} alt=''/> : ''} 
              </p>
              <span className={styles.coin}>
                <span></span>
                <i>{item.getTokenSymbol}</i>
              </span>
            </div>
          </div>
          <div className={styles.timeBar}>
            <div className={statusClassHandle(item.status)}>{statusTextHandle(item.status)}</div>
              <p className={styles.time}>
              {
                [1,2].indexOf(item.status) !== -1 ? (
                  <CurrentCountDown 
                    endTime={item.status === 1 ? item.startTime: item.endTime } 
                    nowTime={imo.serverTime}  
                    key={Math.random()*1000}
                  />
                ) : ""
              }
              </p>
            <div className={styles.coinPic}>
              <Tooltip placement="right" title={`${activeTextObj['popText1']} ${transerTp} token`}>
                <img src={_coinAddress(sk, item.toTokenAddr)} alt='' />
              </Tooltip>
            </div>
          </div>
        </div>
      </div>
      {
        undone ? (
          <div className={styles.topic} style={contSet.mtDis(5)}>
            <img src={warn} alt=''/>{activeTextObj['overTopic']}
          </div>
        ):''
      }
      {
        [2, 3, 4].includes(item.status) ? (
          <Strip param={item} />
        ) : (
          <div className={styles.cardFot}>
            <div className={styles.desc}>
              <p>{activeTextObj['aofr']}</p>
              <p>{`${item.toTokenTotalAmount} ${transerTp}`} </p>
              <p>≈ $12.36</p>
            </div>
            <div className={styles.desc}>
              <p>{activeTextObj['pric']}</p>
              <p>{`1${item.getTokenSymbol} = ${ratio} ${transerTp}`}</p>
              <p>{`≈ ${price}`}</p>
            </div>
          </div>
        )
      }
      {
        (props.subscript === 3 ? (
          <div className={styles.cardFot} style={undone ? contSet.mtDis(10) : {}}>
            <div className={styles.desc}>
              <p>{tabTextObj['MyIn']}</p>
              <p>{`${item.tokenMinAllocation} ${transerTp}`} </p>
              <p>≈ $12.36</p>
            </div>
            <div className={styles.desc}>
              <p>{investmentTextObj['Aow']}</p>
              <p title='qwasd'>--</p>
              <p>≈ $12.36</p>
            </div>
            <div className={styles.desc}>
              <p>{activeTextObj['gtok']}</p>
              <p>--</p>
            </div>
          </div>
        ) : (
          <div className={styles.cardFot}>
            <div className={styles.desc}>
              <p>{activeTextObj['Min']}</p>
              <p>{`${item.toTokenMinAmount} ${transerTp}`} </p>
              <p>≈ $12.36</p>
            </div>
            <div className={styles.desc}>
              <p>{activeTextObj['Max']}</p>
              <p title='qwasd'>{`${item.toTokenMaxAmount} ${transerTp}`} </p>
              <p>≈ $12.36</p>
            </div>
            <div className={styles.desc}>
              <p>Access</p>
              <p>
                <span style={contSet.prDis(5)}>{transferTypeHandle(item.type)}</span>
                <Tooltip placement="bottom" title={activeTextObj['popText2']}>
                  <img src={topic} alt='' />
                </Tooltip>
              </p>
            </div>
          </div>
        ))
      }
    </div>
  )
}

export default observer(Card)
